<template>
<div class="bump64">
    <div class="row">
      <div class="col-xs-12">
        <div class="sectionHeader">
          <h1>FORGOTTEN PASSWORD</h1>   
        </div>
      </div>
    </div>
    <br/>
    <div class="row">
      <div class="col-xs-12 col-sm-8 col-sm-offset-2 centered">
        <div v-if="!sent">>
          <p>To retrieve your password, please enter your email address and press submit.  It could take up to 24 hours to receive a response. </p>
          <p>        
            <input type="text" class="form-control" v-model="input.email" placeholder="EMAIL ADDRESS"><br/>
          </p>
          <div style="width:100%; text-align:center;">
            <div class="centered">
              <button type="button" class="btn btn-orange btn-lg btn-block" v-on:click="sendMessage()">SUBMIT MESSAGE</button>
              <br/>
            </div>
          </div>
        </div>
        <div v-else>
          <p>A temporary password is being emailed to the address on file. Thank you!</p>
          <p><router-link to="/" >BACK TO SIGN-IN</router-link></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {api} from '../../utils/api.js'
import {storage} from '../../utils/storage.js'

export default {
  name : 'Forgot',
  data(){
      return {
      input: { email: ''},
      sent: false
    }
  },
  methods : {
    sendMessage () {
      let msg = {
        email: this.input.email
      };
      api.rawPost('/forgot', msg).then( response => {})
      .catch( error => {
        alert(error)
      });
      this.sent = true;
    },
    signOut() {
      storage.clearValue('acct');
      storage.clearValue('token');
      storage.clearValue('isGood');
      this.loggedIn = false;
      this.$router.push('/');
    }
  }
}

</script>

<style>

</style>


